<template>
  <div class="container">
    <div class="row">
      <div class="col-xs-12">
        <br>
        <h2>Wallet Balance Top-up</h2>
        <hr>
        <br>
      </div>
    </div>
    <div class="row">
      <div class="col-xs-12">
        <form>
          <div class="form-group">
            <h4>Top-up value (SGD)</h4>
            <select class="form-control" v-model="topUpValue">
              <option value="20">SGD 20</option>
              <option value="35">SGD 35</option>
              <option value="50">SGD 50</option>
            </select>
          </div>
          <br>
          <div class="form-group">
            <h4>Preferred payment method</h4>
            <select class="form-control" v-model="topUpMethod" required>
              <option value="">-- Select --</option>
              <option value="visa">Visa</option>
              <option value="master">Master</option>
              <option value="voucher">Voucher</option>
            </select>
          </div>
          <div class="form-group" v-if="topUpMethod.toUpperCase() == 'VOUCHER'">
            <br>
            <h4>Preferred payment method</h4>
            <input type="text" class="form-control" v-model="voucherCode" placeholder="Type your voucher code here">
          </div>
        </form>
        <br>
      </div>
    </div>
    <div class="row">
      <div class="col-xs-12">
        <h4>Payment summary (SGD)</h4>
        <div class="text-xs-center">
          <h3>
              <b>SGD {{ topUpValue }}</b> with <b>
                <span v-if="topUpMethod.length >= 1">{{ topUpMethod | capitalize }}</span>
                <span v-if="topUpMethod.length<= 0"><small><i>select payment method</i></small></span>
              </b>
              <span v-if="topUpMethod.toUpperCase() == 'VOUCHER'"><small>
                <i v-if="voucherCode.length >= 1">
                  ( {{ voucherCode.toUpperCase() }} )
                </i>
                <i v-if="voucherCode.length <= 0">
                  ( Insert your voucher code )
                </i>
              </small></span>
          </h3>
          <br>
        </div>
      </div>
    </div>
    <div class="row" v-if="!isTopUpSuccess && !['VISA', 'MASTER', 'AMEX'].includes(topUpMethod.toUpperCase())">
      <div class="col-xs-12">
        <button type="button" class="btn btn-primary btn-lg btn-block" @click="submitTopUpRequest">Submit</button>
      </div>
    </div>
    <div class="row" v-if="['VISA', 'MASTER', 'AMEX'].includes(topUpMethod.toUpperCase())">
      <div class="col-xs-12 text-xs-center">
        <hr>
        <Card class='stripe-card form-control text-xs-center'
          :class='{ complete }'
          stripe='pk_test_wqTOBW4qf7anhVBuevR50h27'
          :options='stripeOptions'
          @change='complete = $event.complete'
        />
        <!-- <button class='pay-with-stripe' @click='pay' :disabled='!complete'>Pay with credit card</button> -->
        <hr>
      </div>
    </div>
    <div class="row" v-if="!isTopUpSuccess && ['VISA', 'MASTER', 'AMEX'].includes(topUpMethod.toUpperCase()) && complete">
      <div class="col-xs-12">
        <button type="button" class="btn btn-primary btn-lg btn-block" @click="pay">Submit</button>
      </div>
    </div>
    <div class="row" v-if="isTopUpSuccess">
      <div class="col-xs-12">
        <div class="alert alert-success" role="alert">
          Success top-up! 
            <router-link
              :to="{ name: 'wallet' }"
            >
              Click here to view your wallet
            </router-link>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
// import { stripeKey, stripeOptions } from './stripeConfig.json'

import { Card, createToken } from 'vue-stripe-elements-plus'

export default {
  name: "viewTopUpWallet",
  components: {
    Card,
  },
  data () {
    return {
      isTopUpSuccess: false,
      topUpValue: 20,
      topUpMethod: '',
      voucherCode: '',
      complete: false,
      stripeOptions: {
        // see https://stripe.com/docs/stripe.js#element-options for details
          hidePostalCode: true
      }
    }
  },
  methods: {
    pay () {
      // createToken returns a Promise which resolves in a result object with
      // either a token or an error key.
      // See https://stripe.com/docs/api#tokens for the token object.
      // See https://stripe.com/docs/api#errors for the error object.
      // More general https://stripe.com/docs/stripe.js#stripe-create-token.
      createToken().then(data => console.log(data.token))
    },

    submitTopUpRequest () {
      // console.log(this.topUpValue)
      // console.log([20, 35, 50].includes(parseInt(this.topUpValue)))
      if(!([20, 35, 50]).includes(parseInt(this.topUpValue))){
        alert("Please select valid top-up value")
        return;
      }
      if((['VISA', 'MASTER']).includes(this.topUpMethod.toUpperCase())){
        alert("Processing payment with card");
        this.isTopUpSuccess = true;
      }else if(!(['VISA', 'MASTER', 'VOUCHER']).includes(this.topUpMethod.toUpperCase())){
        alert("Please select valid preferred payment method")
        return;
      }
      
      if((['VOUCHER']).includes(this.topUpMethod.toUpperCase())){
        if(this.voucherCode.length >= 6){
          alert("Processing payment with voucher");
          this.isTopUpSuccess = true;
        }else{
          alert("Please provide valid voucher code to proceed with top-up")
          return;
        }
      }
      
    }
  }
};
</script>

<style scoped>
.stripe-card {
  border: 1px solid grey;
}
.stripe-card.complete {
  border-color: green;
}
</style>


