<template>
  <div id="app">
    <GeneralHeader />
      <div class="container_">
        <router-view />
      </div>
    <GeneralFooter />
    <fab v-if="(currentCart.items.length  > 0)"
         :main-icon="fabMainIcon"
         :actions="fabActions"
         :position="fabPosition"
         :bg-color="fabBgColor"
         @gotoWallet="gotoWallet"
         @gotoCart="gotoCart"
         @gotoHome="gotoHome"
    ></fab>
    <fab v-if="(currentCart.items.length <= 0)"
         :main-icon="fabMainIcon"
         :actions="fabActionsWOCart"
         :position="fabPosition"
         :bg-color="fabBgColor"
         @gotoWallet="gotoWallet"
         @gotoCart="gotoCart"
         @gotoHome="gotoHome"
    ></fab>
  </div>
</template>

<script>
import { mapGetters } from "vuex";

import GeneralHeader from "@/components/GeneralHeader";
import GeneralFooter from "@/components/GeneralFooter";

import fab from 'vue-fab';

export default {
  name: 'App',
  components: {
    GeneralHeader,
    GeneralFooter,
    fab
  },
  data () {
    return {
      fabMainIcon: 'pages',
      fabBgColor: '#ffba00',
      fabPosition: 'bottom-right',
      fabActions: [
          {
            name: 'gotoHome',
            icon: 'home'
          },
          {
            name: 'gotoWallet',
            icon: 'monetization_on'
          },
          {
            name: 'gotoCart',
            icon: 'shopping_cart'
          },
      ],
      fabActionsWOCart: [
          {
            name: 'gotoHome',
            icon: 'home'
          },
          {
            name: 'gotoWallet',
            icon: 'monetization_on'
          },
      ]
    }
  },
  methods: {
    gotoWallet(){
      this.$router.push({ name: 'wallet', })
    },
    gotoCart(){
      this.$router.push({ name: 'cart', })
    },
    gotoHome(){
      this.$router.push({ name: 'home', })
    }
  },
  computed: {
    ...mapGetters(["currentCart"])
  },
}
</script>

<style scoped>
.container_{
  margin-top: 78px;
}
</style>


