import ApiService from "@/common/api.service";
import JwtService from "@/common/jwt.service";

import {
  LOGIN,
  LOGOUT,
  REGISTER,
  CHECK_AUTH,
  UPDATE_USER
} from "@/store/actions.type";

import { SET_AUTH, PURGE_AUTH, SET_ERROR } from "@/store/mutations.type";

const state = {
  errors: null,
  user: {
    id: '',
    username: '',
    email: '',
    wallet: {
      ccy: '',
      value: 0,
    },
    token: '',
    lastTransaction: [ ]
  },
  isAuthenticated: !!JwtService.getToken()
};

const getters = {
  currentUser(state) {
    return state.user;
  },
  isAuthenticated(state) {
    return state.isAuthenticated;
  }
};

const actions = {
  [LOGIN](context, credentials) {
    return new Promise(resolve => {
      ApiService.post("users/login", { user: credentials })
        .then(({ data }) => {
          context.commit(SET_AUTH, data.user);
          resolve(data);
        })
        .catch(({ response }) => {
          context.commit(SET_ERROR, response.data.errors);
        });
    });
  },

  [LOGOUT](context) {
    context.commit(PURGE_AUTH);
  },

  [REGISTER](context, credentials) {
    return new Promise((resolve, reject) => {
      ApiService.post("users", { user: credentials })
        .then(({ data }) => {
          context.commit(SET_AUTH, data.user);
          resolve(data);
        })
        .catch(({ response }) => {
          context.commit(SET_ERROR, response.data.errors);
          reject(response);
        });
    });
  },

  [CHECK_AUTH](context) {
    if (JwtService.getToken()) {
      ApiService.setHeader();
      ApiService.get("user/checktoken")
        .then(({ data }) => {

          // console.log(data)
          
          let userData = {
            data: {
              user: {
                id: data.user.id,
                username: data.user.username,
                email: data.user.email,
                wallet: {
                  ccy: 'SGD',
                  value: 18.80,
                },
                token: data.user.token,
                lastTransaction: [
                  {
                    id: 'EC374EBB-448C-4E49-A954-D73FC3AEA8CF',
                    merchant: 'Thirsty Guys',
                    value: '3.5',
                    dateTime: '19 February 2019 19:48',
                  },
                ],
              }
            }
          }

          context.commit(SET_AUTH, userData.data.user);
        })
        .catch(({ response }) => {
          context.commit(SET_ERROR, response.data.errors);
        });

      

    } else {
      context.commit(PURGE_AUTH);
      return false
    }
  },

  [UPDATE_USER](context, payload) {
    const { email, username, } = payload;
    const user = {
      email,
      username,
    };
    if (password) {
      user.password = password;
    }

    return ApiService.put("user", user).then(({ data }) => {
      context.commit(SET_AUTH, data.user);
      return data;
    });
  }
};

const mutations = {
  [SET_ERROR](state, error) {
    state.errors = error;
  },

  [SET_AUTH](state, user) {
    // console.log(user)
    state.isAuthenticated = true;
    state.user = user;
    state.errors = {};
    JwtService.saveToken(state.user.token);
  },

  [PURGE_AUTH](state) {
    state.isAuthenticated = false;
    state.user = {};
    state.errors = {};
    JwtService.destroyToken();
  }
};

export default {
  state,
  actions,
  mutations,
  getters
};


